<h1 id="oa" tabindex="-1"><a class="header-anchor" href="#oa" aria-hidden="true">#</a> OA</h1>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token comment">//...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="获取打卡数据" tabindex="-1"><a class="header-anchor" href="#获取打卡数据" aria-hidden="true">#</a> 获取打卡数据</h2>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">checkinRecords</span><span class="token punctuation">(</span><span class="token keyword type-hint">int</span> <span class="token variable">$startTime</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$endTime</span><span class="token punctuation">,</span> <span class="token keyword type-hint">array</span> <span class="token variable">$userList</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$type</span> <span class="token operator">=</span> <span class="token number">3</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>{info} $type: 打卡类型 1：上下班打卡；2：外出打卡；3：全部打卡</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// 全部打卡数据</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oa</span><span class="token operator">-></span><span class="token function">checkinRecords</span><span class="token punctuation">(</span><span class="token number">1492617600</span><span class="token punctuation">,</span> <span class="token number">1492790400</span><span class="token punctuation">,</span> <span class="token punctuation">[</span><span class="token string double-quoted-string">"james"</span><span class="token punctuation">,</span><span class="token string double-quoted-string">"paul"</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取上下班打卡</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oa</span><span class="token operator">-></span><span class="token function">checkinRecords</span><span class="token punctuation">(</span><span class="token number">1492617600</span><span class="token punctuation">,</span> <span class="token number">1492790400</span><span class="token punctuation">,</span> <span class="token punctuation">[</span><span class="token string double-quoted-string">"james"</span><span class="token punctuation">,</span><span class="token string double-quoted-string">"paul"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token number">1</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取外出打卡</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oa</span><span class="token operator">-></span><span class="token function">checkinRecords</span><span class="token punctuation">(</span><span class="token number">1492617600</span><span class="token punctuation">,</span> <span class="token number">1492790400</span><span class="token punctuation">,</span> <span class="token punctuation">[</span><span class="token string double-quoted-string">"james"</span><span class="token punctuation">,</span><span class="token string double-quoted-string">"paul"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="获取审批数据" tabindex="-1"><a class="header-anchor" href="#获取审批数据" aria-hidden="true">#</a> 获取审批数据</h2>
<p>API:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword type-declaration">mixed</span> <span class="token function">approvalRecords</span><span class="token punctuation">(</span><span class="token keyword type-hint">int</span> <span class="token variable">$startTime</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$endTime</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$nextNumber</span> <span class="token operator">=</span> <span class="token constant">null</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>{info} $nextNumber: 第一个拉取的审批单号，不填从该时间段的第一个审批单拉取</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oa</span><span class="token operator">-></span><span class="token function">approvalRecords</span><span class="token punctuation">(</span><span class="token number">1492617600</span><span class="token punctuation">,</span> <span class="token number">1492790400</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 指定第一个拉取的审批单号，不填从该时间段的第一个审批单拉取</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">oa</span><span class="token operator">-></span><span class="token function">approvalRecords</span><span class="token punctuation">(</span><span class="token number">1492617600</span><span class="token punctuation">,</span> <span class="token number">1492790400</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'201704240001'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div>